	module.exports = {"data":{"markdownRemark":{"html":"<p>The policy section enables organizations to publish information about their program in order to communicate the specifics about their program to hackers. Organizations typically publish a vulnerability disclosure policy with guidance on how they want to receive information related to potential vulnerabilities in their products or online services. </p>\n<p>The policy also inclues your program’s scope which is the list of items you'd like hackers to test and send reports in for. It is often defined by the domain name for web applications, or by the specific App Store / Play store mobile apps that your company builds. </p>\n<p><img src=\"./images/policy-and-scope-1.png?raw=true\" alt=\"Policy Scope\"></p>","frontmatter":{"path":"/programs/policy-and-scope","title":"Policy and Scope"}}},"pathContext":{}}

